import { Component, OnInit } from '@angular/core';
import { HttpService } from '../../api/http.service';
import { Router, RouterEvent } from '@angular/router';
import { Events } from '@ionic/angular';
@Component({
  selector: 'app-login',
  templateUrl: './login.page.html',
  styleUrls: ['./login.page.scss'],
  providers: [HttpService]
})
export class LoginPage implements OnInit {
  currentColor:any;
  username:any;
  password:any;
  response:any;
  
  constructor(public events: Events,private router: Router,public shared: HttpService) {
    this.currentColor = 'light';
   }

  ngOnInit() {
  }

  
  
login(){

 
  
  let name=this.username;
  let password=this.password;

if(name==undefined || name==""||password==undefined||password==""){
this.shared.displayNetworkUpdate('Username and password Cannot be blank')
}else{
  if (navigator.onLine) {
    this.shared.ServiceCallLogin.ServiceCallRequestLoginData(name,password).then((res) => { 
      this.response=res;
      
      if(this.response.user_id==""){
        this.shared.displayNetworkUpdate(this.response.content)
      }else{
        this.events.publish('login',Date.now());
        localStorage.setItem('login','true');
      localStorage.setItem('profile', JSON.stringify(this.response));
       
        this.router.navigate(['/menu/first/tabs/tab1']); 
        
      }

      
      }, err => {
   
      })

  }else{
    this.shared.displayNetworkUpdate('No Internet Connection')

  }

}

 
}


forgotPassword(){

  this.router.navigate(['/menu/first/tabs/tab1/login/forgot']); 
}


loginback(){
  this.router.navigate(['/menu/first/tabs/tab1']);
}


}
